@import "variables";

.header__container {
   .header__page {
      @extend %flex-sb;
      height: 116px;
      .header__nav-bar-menu {
         .nav-bar__menu {
            display: flex;
            gap: 40px;
            .menu__list {
               list-style: none;
               .menu__list-link {
                  @extend %nav-bar-title;
                  &:hover {
                     color: #1C2A39;
                  }
               }
            }
         }
      }
      .header__nav-bar-loby {
         .nav-bar__loby {
            display: flex;
            gap: 40px;
            .nav-bar__loby-list {
               list-style: none;
            }
         }
      }
   }
}

.active-link {
   font-family: 'Montserrat';
   font-style: normal;
   font-weight: 900;
   font-size: 10px;
   line-height: 12px;
   text-transform: uppercase;
   color: #1C2A39;
}