@import "variables";
@import "wrapper";

.list-books__conteiner {
}
.list-books__body {
}
.list-books__cards {
   display: flex;
   flex-wrap: wrap;
   justify-content: space-between;
   width: 924px;
   margin-left: auto;
}
.list-books__card {
   display: flex;
   width: 424px;
   gap: 36px;
}
.list-books__card--content {
   display: flex;
   flex-direction: column;
   justify-content: center;
}
.list-books__card--autor {
   @extend %card-subtitle;
}
.list-books__card--name {
   @extend %card-title;
   margin: 4px 0;
}
.list-books__card--reviews {
   display: flex;
   gap: 6px;
}
.list-books__card--description {
   @extend %card-subtitle;
   margin: 16px 0;
}
.list-books__card--price {
   @extend %card-price;
}
.list-books__card--button {
   margin-top: 16px;
   border: 1px solid #4C3DB2;
   background-color: inherit;
   width: 176px;
   height: 45px;
   display: flex;
   align-items: center;
   justify-content: center;
   color: #4C3DB2;
   font-family: 'Montserrat';
   font-size: 8px;
   font-style: normal;
   font-weight: 700;
   line-height: normal;
   text-transform: uppercase;
}